.EventsTab {
  width: inherit;
  overflow-wrap: break-word;
  position: relative;
}
.EventsTab-topGradient {
  position: absolute;
  width: 100%;
  height: 9em;
  background: linear-gradient(
    180deg,
    rgba(91, 91, 91, 0.1) 0%,
    rgba(91, 91, 91, 0) 100%
  );
  z-index: -1;
}

.Days-list {
  padding-inline-start: 0;
}
